.Bipra {
    height: 600px;
    padding: 20px;

    .TableBipra{
        border-collapse: collapse;
        margin: 25px 0;
        font-size: 0.9em;
        min-width: 400px;
        border-radius: 5px 5px 0 0;
        overflow: hidden;
        width: 100%;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    }

    .TableBipra thead tr{
        background-color: #3c8cc8;
        color: white;
        text-align: left;
        font-weight: bold;
    }

    .TableBipra th,
    .TableBipra td {
        padding: 12px 15px;
        text-align: justify;

    }
    .TableBipra tbody tr{
        border-bottom: 1px  solid #dddddd;
        
    }

    .TableBipra tbody tr:nth-of-type(even){
        background-color: #f3f3f3;
    }
        
    .TableBipra tbody tr:last-of-type {
        border-bottom: 2px solid #3c8cc8;
    }
    button {
        margin-left: 3px;
        margin-top: 3px;

    }
    
  



    
}

img {
    width: 300px;
    margin : 10px;
        
}

