.Usersss {
    height: 600px;
    padding: 20px;

    .TableUsersss{
        border-collapse: collapse;
        margin: 25px 0;
        font-size: 0.9em;
        min-width: 400px;
        border-radius: 5px 5px 0 0;
        overflow: hidden;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    }

    .TableUsersss thead tr{
        background-color: #3c8cc8;
        color: white;
        text-align: left;
        font-weight: bold;
    }

    .TableUsersss th,
    .TableUsersss td {
        padding: 12px 15px;

    }
    .TableUsersss tbody tr{
        border-bottom: 1px  solid #dddddd;
        
    }

    .TableUsersss tbody tr:nth-of-type(even){
        background-color: #f3f3f3;
    }
        
    .TableUsersss tbody tr:last-of-type {
        border-bottom: 2px solid #3c8cc8;
    }


    
}
