.Berita {
    display: flex;
    width: 100%;
    .listContainer {
      flex: 6;

      h2{
        text-align: center;
      }
    }

  }
  .list-container {
    border: 1px solid rgb(221, 221, 221);
    border-radius: 5px;
  }
  
  .container {
    width: 100%;
    margin: auto;
    overflow: hidden;
  }
  
  input {
    width: 100%;
    margin-bottom: 3px;
  }
  
  textarea {
    width: 100%;
    margin-bottom: 3px;
  }
  
  img {
    width:200px;
    height: auto;
  }
  
  .dateText {
    font-style: italic;
    font-size: small;
    color: rgb(196, 196, 196);
  }
  
  .inputError {
    color:red;
  }
  
  