.Tabel {
    height: 600px;
    padding: 20px;

    .TabelInti{
        border-collapse: collapse;
        margin: px 0;
        font-size: 0.9em;
        min-width: 400px;
        border-radius: 5px 5px 0 0;
        overflow: hidden;
        width: 100%;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    }

    .TabelInti thead tr{
        background-color: #3c8cc8;
        color: white;
        text-align: left;
        font-weight: bold;
    }

    .TabelInti th,
    .TabelInti td {
        padding: 12px 15px;
        text-align: justify;

    }
    .TabelInti tbody tr{
        border-bottom: 1px  solid #dddddd;
        
    }

    .TabelInti tbody tr:nth-of-type(even){
        background-color: #f3f3f3;
    }
        
    .TabelInti tbody tr:last-of-type {
        border-bottom: 2px solid #3c8cc8;
    }
    button {
        margin-left: 3px;

    }


    
}
